/* styles reset */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  font-size: 62.5%; /* 1rem = 10px */
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: inherit;
}

ol, ul {
	list-style: none;
}

button {
  font-family: inherit;
}